import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController } from 'ionic-angular';

import { LocalService } from '../../services/local.service';
import { CrudService } from '../../services/crud.service';

import { iShop } from '../../interfaces/shop.interface';
import { iItem } from '../../interfaces/item.interface';
@IonicPage()
@Component({
  selector: 'page-remove',
  templateUrl: 'remove.html',
})
export class RemovePage {
  SHOP: iShop;
  ITEMS: iItem[]=[];
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private modalCtrl: ModalController,
    private localService: LocalService,
    private crudService: CrudService
  ) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad RemovePage');
  }

  go2SearchShop() {
    let modal = this.modalCtrl.create('SearchShopPage');
    modal.onDidDismiss((data) => {
      console.log(data);
      this.SHOP = data.SHOP;
      this.localService.getSHOP_ITEMSnSHOP_ITEMS_ID(this.SHOP.SHOP_ID)
      .then((res: any)=>{
        console.log(res);
        this.ITEMS = res.SHOP_ITEMS
      })
      .catch((err)=>{ console.log(err)})
    })
    modal.present().catch((err) => { console.log(err) });
  }

  deleteItem(item){
    console.log(item);
    this.crudService.deleteItem(item);
  }

}



